/*eslint-disable*/module.exports={messages:{"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels im Verzug** ist oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst dann selbstständig beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben und der Mangel danach nicht behoben wurde.":"- Ein Anspruch auf **Aufwendungsersatz** ist das Recht auf **Erstattung der Kosten**, die bei der **Beseitigung des Mangels** entstanden sind.\n- Sie dürfen den Mangel selbst beseitigen, wenn die Vermieterin **mit der Beseitigung des Mangels im Verzug** ist oder es sich um eine **Not- oder Eilmaßnahme** handelt.\n- Meistens sollten Sie den Mangel erst dann selbstständig beseitigen, wenn Sie Ihre Vermieterin mit einer Frist zur Beseitigung aufgefordert haben und der Mangel danach nicht behoben wurde.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.":"- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annullierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1.500km)**, **400€ (zwischen 1.500km und 3.500km)** oder **600€ (über 3.500km)**.","- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.":"- Ein Anspruch auf **Betreuungsleistungen** ist das Recht auf zusätzliche Unterstützung wie Essen und Getränke, wenn sich Ihr **Abflug stark verzögert**.\n- Betreuungsleistungen müssen **angemessen zur Wartezeit und Tageszeit** sein. (Kleine) Mahlzeiten und Getränke sowie Zugang zu  Telefon oder Internet sind immer angemessen.\n- Wenn **lange Wartezeiten am Flughafen bei Nacht** entstehen, kann auch das Recht auf eine **Hotelunterbringung** mit Transport dorthin bestehen. \n- In der Regel werden Ihnen diese Leistungen **von der Fluglinie angeboten**; wenn nicht, können Sie **Schadensersatz** für notwendige Ausgaben mit Rechnungen beantragen.","- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch ist und die Wohnqualität stark beeinträchtigt wird.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.":"- Ein Anspruch auf **Mietminderung** ist das Recht, eine **geringere Gesamtmiete zu zahlen**, wenn die **Nutzung der Wohnung**\ndurch einen Mangel **erheblich eingeschränkt** ist. \n- Ein **erheblicher Mangel** liegt vor, wenn der Mangel nicht nur optisch ist und die Wohnqualität stark beeinträchtigt wird.\n- Der Zeitraum der Minderung **beginnt mit der Anzeige des Mangels** bei der Vermieter:in und **endet mit der Beseitigung**.\n- Sie sollten nicht sofort eine geringere Miete zahlen, da Sie so eine Kündigung wegen Mietverzug riskieren. Stattdessen können Sie Ihrer Vermieterin melden, die **Miete unter Vorbehalt zu zahlen**. Nach Beseitigung des Mangels können Sie dann einen Anteil zurückfordern.\n- Um **wie viel Prozent** die Gesamtmiete gekürzt werden kann, hängt von der **Schwere des Mangels** ab. Einen Anhaltspunkt für Ihren Fall können Sie in einer **Mietminderungstabelle** im Internet finden.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro verantwortlich sein, wenn das im Mietvertrag festgelegt wurde.":"- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mietende können für Kleinreperaturen bis etwa 100 Euro verantwortlich sein, wenn das im Mietvertrag festgelegt wurde.","- Ein Anspruch auf **Rückerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zurückzuerhalten.\n- Zusätzlich erhalten Sie einen Rückflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der Rückerstattung können Sie auch eine anderweitige Beförderung zu Ihrem Zielort durch die Fluglinie wählen.":"- Ein Anspruch auf **Rückerstattung der Flugscheinkosten** ist das Recht, Ihren gezahlten Ticketpreis bei Annullierung innerhalb einer Woche zurückzuerhalten.\n- Zusätzlich erhalten Sie einen Rückflug zu Ihrem ersten Abflugort, wenn ein Anschlussflug annuliert wurde.\n- Statt der Rückerstattung können Sie auch eine anderweitige Beförderung zu Ihrem Zielort durch die Fluglinie wählen.","- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln** oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist oder diesen bei Vertragsabschluss verschwiegen hat.":"- Ein Anspruch auf **Schadensersatz** ist das Recht, sich **Geld** für durch den Mangel **entstandene Schäden erstatten** zu lassen.\n- **Mögliche Schäden** sind entstandene **Hotelkosten**, Wertverlust bei unbrauchbar gewordenen **Möbeln** oder auch Schmerzensgeld bei körperlichen Schäden.\n- Wichtig ist, dass die Vermieter:in den Mangel zu vertreten hat, mit der Beseitigung in Verzug ist oder diesen bei Vertragsabschluss verschwiegen hat.","<0>Streitwert</0><1>Gerichtsgebühren<2/>(Vorschuss)</1><3>Anwaltshonorar<4/>(pro Seite)</3>":"<0>Streitwert</0><1>Gerichtsgebühren<2/>(Vorschuss)</1><3>Anwaltshonorar<4/>(pro Seite)</3>","Abokündigung":"Abokündigung","Aufwendungsersatz":"Hoàn trả chi phí","Ausgleichszahlung":"Ausgleichszahlung","Außergerichtliche Einigung":"Außergerichtliche Einigung","Beratungsangebote":"Beratungsangebote","Beseitigung des Mangels durch die Vermieter:in":"Loại bỏ các khiếm khuyết của chủ nhà","Betreuungsleistungen":"Betreuungsleistungen","Datenschutz":"Datenschutz","Der Justizportal Wegweiser informiert Sie über Ihre Rechte im Alltag und hilft Ihnen Handlungsoptionen zur Lösung Ihres Rechtsproblems zu finden. Probieren Sie es hier aus!":"Der Justizportal Wegweiser informiert Sie über Ihre Rechte im Alltag und hilft Ihnen Handlungsoptionen zur Lösung Ihres Rechtsproblems zu finden. Probieren Sie es hier aus!","Die Justiz im Netz.":"Các cơ quan tư pháp trên mạng.","Diese Option ist leider noch nicht verfügbar.":"Thật không may, tùy chọn này chưa có sẵn.","Dokumentation des Mangels":"Tài liệu về khiếm khuyết","Durch die Beantwortung einfacher Ja-Nein-Fragen helfen wir Ihnen, relevante und kostenfreie Rechtsinformationen zu finden.":"Durch die Beantwortung einfacher Ja-Nein-Fragen helfen wir Ihnen, relevante und kostenfreie Rechtsinformationen zu finden.","Egal ob Sie Probleme mit Schimmel, Lärm oder einem Wasserschaden haben - hier können Sie sich über Ihre Rechte bei Mietmängeln informieren!":"Egal ob Sie Probleme mit Schimmel, Lärm oder einem Wasserschaden haben - hier können Sie sich über Ihre Rechte bei Mietmängeln informieren!","Einfach, online, jederzeit.":"Dễ dàng, trực tuyến, bất cứ lúc nào.","Erstberatung":"Erstberatung","Erstberatung bei der Verbraucherzentrale":"Erstberatung bei der Verbraucherzentrale","Fall Sie sich unsicher fühlen, können Sie sich auch beraten lassen. Dafür haben wir <0>hier Beratungsangebote zusammengefasst.</0>":"Fall Sie sich unsicher fühlen, können Sie sich auch beraten lassen. Dafür haben wir <0>hier Beratungsangebote zusammengefasst.</0>","Flugentschädigungen":"Flugentschädigungen","Fluglinie kontaktieren":"Fluglinie kontaktieren","Gebaut von <0>Tech4Germany</0>":"Gebaut von <0>Tech4Germany</0>","Gerichtsprozesse folgen einem festen Ablauf, der im Gesetz geregelt ist. Dabei gibt es viele Sonderregelungen. Um Ihnen einen Überblick zu geben, haben wir hier eine vereinfachte Grafik erstellt.":"Gerichtsprozesse folgen einem festen Ablauf, der im Gesetz geregelt ist. Dabei gibt es viele Sonderregelungen. Um Ihnen einen Überblick zu geben, haben wir hier eine vereinfachte Grafik erstellt.","Gerichtsverfahren":"Gerichtsverfahren","Handlungsoptionen vergleichen":"So sánh các tùy chọn cho hành động","Ihr Flug war verspätet, wurde annulliert oder Ihnen wurde die Beförderung verweigert? Informieren Sie sich hier über mögliche Ansprüche aus der EU-Fluggastrechteverordnung!":"Ihr Flug war verspätet, wurde annulliert oder Ihnen wurde die Beförderung verweigert? Informieren Sie sich hier über mögliche Ansprüche aus der EU-Fluggastrechteverordnung!","Ihr Vertrag wurde unerwartet verlängert oder Ihnen sind unvorhergesehene Kosten entstanden? In Zukunft informieren wir Sie hier über Ihre Rechte bei Abofallen.":"Ihr Vertrag wurde unerwartet verlängert oder Ihnen sind unvorhergesehene Kosten entstanden? In Zukunft informieren wir Sie hier über Ihre Rechte bei Abofallen.","Impressum":"Impressum","Info":"Thông tin","Ja":"Có","Jetzt starten!":"Bắt đầu ngay bây giờ","Landesspezifische Schlichtungsstellen":"Landesspezifische Schlichtungsstellen","Mehr erfahren":"Mehr erfahren","Mietervereine / Deutscher Mieterbund":"Mietervereine / Deutscher Mieterbund","Mietminderung":"Giảm tiền thuê","Mietmängel":"Mietmängel","Mit Ihren Angaben können wir für Sie mögliche rechtliche Ansprüche zusammenfassen, über die Sie sich in Ruhe informieren können.":"Mit Ihren Angaben können wir für Sie mögliche rechtliche Ansprüche zusammenfassen, über die Sie sich in Ruhe informieren können.","Mängelanzeige bei Ihrer Vermieter:in":"Thông báo khuyết tật cho chủ nhà của bạn","Mögliche Ansprüche":"Mögliche Ansprüche","Mögliche Lösungen":"Mögliche Lösungen","Nein":"Không","Nur kostenlose Angebote anzeigen":"Nur kostenlose Angebote anzeigen","Nächste Schritte":"Nächste Schritte","Online Klage einreichen":"Tộp đơn kiện trực tuyến","Optionen":"Optionen","Problem einordnen":"Problem einordnen","Rechtlich beraten lassen":"Nhận tư vấn pháp lý","Rechtsantragsstelle am Amtsgericht":"Rechtsantragsstelle am Amtsgericht","Rechtsberatung bei Anwält:innen":"Rechtsberatung bei Anwält:innen","Rechtsproblem einordnen":"Phân loại vấn đề pháp lý","Rückerstattung der Flugscheinkosten":"Rückerstattung der Flugscheinkosten","Schadensersatz":"Thiệt hại","Schlichtungsantrag stellen":"Schlichtungsantrag stellen","Startseite":"Startseite","Städtische Mietberatungen":"Städtische Mietberatungen","Unser Angebot":"Đề nghị của chúng tôi","Vermieter:in erneut kontaktieren":"Vermieter:in erneut kontaktieren","Warum?":"Warum?","Wegweiser":"Wegweiser","Weiter Fragen":"Weiter Fragen","Weitere Möglichkeiten":"Weitere Möglichkeiten","Welche Kosten entstehen in einem Gerichtsprozess?":"Welche Kosten entstehen in einem Gerichtsprozess?","Wie es funktioniert":"Cách hoạt động","Wie läuft ein Gerichtsprozess ab?":"Wie läuft ein Gerichtsprozess ab?","Wobei haben Sie ein Problem?":"Wobei haben Sie ein Problem?","Wobei können wir Ihnen helfen?":"Chúng tôi có thể giúp gì cho bạn?","ZPO Informationen":"ZPO Informationen","Zuletzt geben wir Ihnen mehrere Handlungsoptionen und Vorlagen, um Ihre Ansprüche durchzusetzen - von einem Schreiben an die Gegenseite, über eine Rechtbseratung bis hin zu einer Klage vor Gericht.":"Zuletzt geben wir Ihnen mehrere Handlungsoptionen und Vorlagen, um Ihre Ansprüche durchzusetzen - von einem Schreiben an die Gegenseite, über eine Rechtbseratung bis hin zu einer Klage vor Gericht.","Zum Prototypen":"Để nguyên mẫu","Zum Wegweiser":"Zum Wegweiser","Zur Erstellung des Klagedokuments":"Zur Erstellung des Klagedokuments","Zur Klageerstellung":"Zur Klageerstellung","Zur Vorlage für die Mängelanzeige":"Zur Vorlage für die Mängelanzeige","Zur Übersicht für Beratungsangebote":"Zur Übersicht für Beratungsangebote","Zurück":"Quay lại","Zurück zur Homepage":"Zurück zur Homepage","back":"Zurück","banner":"Diese Seite ist ein Prototyp des <0>Tech4Germany Fellowships 2021</0> und bietet keine verbindlichen Rechtsinformationen.","co.info_evv":"**Angebot**: \n- Prüfung von rechtlichen Ansprüchen durch Anwält:innen des Deutschen Mieterbunds in einem Gespräch.\n- Vorschlag von ersten Handlungsoptionen.\n######\n**Verfügbarkeit**: \n- Telefonische oder persönliche Beratung, deutschlandweit. [Hier finden Sie die Website für Ihr Bundesland](https://www.verbraucherzentrale.de/beratung).\n######\n**Kosten**: \n- Circa 20-30 Euro für eine zwanzigminütige Beratung.\n######\nDie Verbraucherzentralen der Bundesländer bieten zusammen mit dem Deutschen Mieterbund kostengünstige Erstberatungen durch Anwält:innen an, [in Baden-Württemberg beispielsweise ab 20 Euro](https://www.verbraucherzentrale-bawue.de/beratungsstellen/746/kontakt/Mietrecht/17938).","co.info_ls":"**Angebot**: \n- Vermittlung zwischen Ihnen und der Gegenseite für eine Einigung durch eine ausgebildete, unabhängige Schiedsperson.\n######\n**Verfügbarkeit**: \n- In einigen Bundesländern, beispielsweise [Berlin](https://www.berlin.de/sen/verbraucherschutz/verbraucherportal/recht/vertragsrecht/schlichtung-261337.php) und [Nordrhein-Westfalen](https://streitschlichtung.nrw.de/JOLStreit/).\n######\n**Kosten**: \n- In Nordrhein-Westfalen beispielsweise zwischen 10 und 40 Euro.\n######\nEinige Bundesländer bieten Schlichtungsstellen, die Bürger:innen bei der gütlichen Beilegung von Konflikten helfen. Dabei vermittelt eine Schlichter:in zwischen beiden Parteien. Diese Stellen helfen besonders, wenn man die Beziehung zur Gegenpartei erhalten möchte - zum Beispiel in einem Mietverhältnis.","co.info_mm":"**Angebot**: \n- Beratung und außergerichtliche Vertretung in allen Mietfragen durch festangestelle Anwält:innen.\n- Auf Wunsch Übernahme der Kommunikation mit Ihrer Vermieter:in.\n- Teilweise Angebote in Drittsprachen.\n######\n**Verfügbarkeit**: \n- In vielen deutschen Städten. [Hier finden Sie den für Sie nähesten regionalen Verein des Deutschen Mieterbunds](https://www.mieterbund.de/beratung/mieterverein-vor-ort.html).\n######\n**Kosten**: \n- Teilweise kostenlose Erstberatung.\n- Jährliche Mitgliedschaften für etwa 100 Euro, Mindestlaufzeit zwei Jahre.\n######\nIn Deutschland gibt es hunderte regionale Mietervereine, in denen Anwält:innen Beratungen für Mitglieder anbieten. Wenn sich in Ihrem Ort kein Verein befindet, ist eine Mitgliedschaft oft auch aus anderen Städten möglich.","co.info_ra":"**Angebot**: \n- Rechtliche Beratung sowie außergerichtliche und gerichtliche Vertretung in allen Rechtsfragen.\n######\n**Verfügbarkeit**: \n- Deutschlandweit.\n- [Über die Anwaltsauskunft](https://anwaltauskunft.de/magazin), ein Portal des Deutschen Anwaltvereins, können Sie eine Anwält:in in Ihrer Nähe finden.\n######\n**Kosten**: \n- Eine Erstberatung kostet maximal 149 Euro, kann aber auch deutlich günstiger sein.\n- Kostenlose Beratung ist mit einer Rechtschutzversicherung oder einem Beratungshilfeschein für finanzschwache Personen möglich.\n- Hier finden Sie einen [Antrag auf Beratungshilfe in Deutsch](https://justiz.de/service/formular/dateien/agI1.pdf) mit zusätzlichen [Hinweisen auf Englisch](https://justiz.de/service/formular/dateien/agl1-hinweisblatt_beratungshilfe_englisch.pdf).\n######\nRechtsanwält:innen können Sie in Rechtsangelegenheiten gegenüber der Gegenseite und vor Gericht vertreten. Jede Bürger:in hat das Recht, sich bei rechtlichen Fragen durch eine Rechtsanwält:in beraten und vertreten zu lassen.","co.info_ram":"**Angebot**: \n- Hilfe bei der Einreichung von Klagen und Anträgen.\n- Beantwortung von Fragen zu Briefen des Gerichts.\n######\n**Verfügbarkeit**: \n- Amtsgerichte gibt es in jeder mittelgroßen deutschen Stadt.\n- Die Rechtantragsstelle können Sie dort persönlich vor Ort oder per Telefon erreichen.\n- [Das nächste Amtsgericht für Ihren Wohnort können sie hier finden](https://www.justizadressen.nrw.de/de/justiz/suche).\n######\n**Kosten**: \n- Kostenlos.\n######\nWenn Sie Hilfe bei der Einreichung einer Klage oder der Beantwortung eines Gerichtsbriefs brauchen, kann Ihnen die Rechtsantragsstelle am Amtsgericht weiterhelfen. Hierfür können Sie einen kostenfreien Termin mit einer ausgebildeten Rechtspfleger:in vereinbaren. Diese stellt sicher, dass Ihre Erklärungen alle rechtlich erforderlichen Informationen erhält. Eine individuelle rechtliche Beratung ist jedoch nicht möglich.","co.info_sm":"**Angebot**:\n- Erste Anlaufstelle für drängende Mietfragen (z.B. Mieterhöhung, Kündigung), teils Beratung in Fremdsprachen.\n######\n**Verfügbarkeit**: \n- Telefonische oder persönliche Beratung in vielen großen Städten, zum Beispiel in [Berlin](https://www.stadtentwicklung.berlin.de/wohnen/mieterberatungen.shtml), [München](http://www.muenchen.de/dienstleistungsfinder/muenchen/1074551/), [Hamburg](https://www.hamburg.de/oera/), [Stuttgart](https://www.stuttgart.de/vv/verwaltungseinheit/beratungstelefon-fuer-mieter-und-vemieter.php) oder [Nürnberg](https://www.nuernberg.de/internet/wohnen/mietrechtsinformationen.html).\n###### \n**Kosten**:\n- Die meisten Angebote sind kostenlos verfügbar.\n######\nEinige Städte bieten kostenfreie Mieterberatung an. Diese Beratungsstellen sind oft für drängende Probleme (fristlose Kündigung, Räumungsklage) oder Bürger:innen mit begrenzten finanziellen Mitteln gedacht.","consultation.header":"Beratungsangebote","consultation.sub_header":"Da das Justizportal Sie nicht in jedem Fall informieren kann, haben wir hier einen Überblick zu kostengünstigen Beratungsstellen für Sie aufbereitet.","exit.footer":"Sie suchen noch weitere Unterstützung? Dann können Sie sich an rechtliche Beratungsstellen wenden, die Sie\nindividuell zu Ihrem Fall informieren können. In einer Beratung beurteilen Expert:innen, ob rechtliche\nAnsprüche bestehen und wie Sie diese am besten durchsetzen können. Dafür haben wir [hier einen Überblick über\nBeratungsstellen aufbereitet](/beratungsangebote).","exit.heading":"Leider haben wir Ihren Fall nicht abgedeckt.","forward":"Weiter","home.hero_offer":"Justizportal.de là một lời đề nghị của Bộ Tư pháp Liên bang và Bảo vệ người tiêu dùng hợp tác với các bộ tư pháp liên bang ở các tiểu bang liên bang. Cổng thông tin cung cấp cho bạn một cái nhìn tổng quan về quyền của bạn trong các tình huống hàng ngày khác nhau. Nó cũng giúp bạn phân loại và giải quyết các câu hỏi và vấn đề pháp lý của bạn. Bạn sẽ được thông báo về các lựa chọn hành động của bạn và sẽ được hỗ trợ trong việc khẳng định quyền của bạn.","home.howitwork_claims":"Sau đó, chúng tôi sẽ giúp bạn đánh giá những xác nhận quyền sở hữu nào có thể tồn tại trong trường hợp của bạn. Khiếu nại là quyền yêu cầu một hành động cụ thể từ người khác (ví dụ như thanh toán thiệt hại), để chịu đựng (ví dụ như cho thuê lại một căn hộ) hoặc để kiềm chế (ví dụ như nhập vào một tài sản). invidunt ut labore.","home.howitwork_classify":"Trước tiên, chúng tôi sẽ giúp bạn làm rõ liệu có vấn đề pháp lý trong trường hợp của bạn hay không. Chúng tôi sẽ cho bạn thấy thông tin phù hợp với trường hợp của bạn.","home.howitwork_heading":"Cách hoạt động","home.howitwork_lawsuit":"Khi tất cả các lựa chọn khác đã hết, chúng tôi sẽ giúp bạn nộp đơn khiếu nại với tòa án quận trực tuyến. Để đạt được điều này, chúng tôi giải thích quá trình của quá trình trực tuyến của chúng tôi cho bạn và cung cấp thông tin minh bạch về các chi phí của thủ tục tố tụng pháp lý cũng như các tư vấn pháp lý có thể.","home.howitwork_options":"Dựa trên điều này, chúng tôi sẽ đề xuất các giải pháp có thể <0> cho bạn - ban đầu cho một giải quyết tranh chấp thân thiện với phía đối diện. </0>","home.howitwork_subheading":"Chúng tôi có thể giúp bạn giải quyết vấn đề pháp lý của mình bằng bốn bước đơn giản:","home.howtohelp_footer":"Bạn có thể tìm hiểu thêm về các vấn đề pháp lý mà chúng tôi có thể giúp bạn trên các trang sau đây.","home.sub_heading":"Chúng tôi thông báo cho bạn về quyền lợi của bạn trong cuộc sống hàng ngày và giúp bạn giải quyết vấn đề pháp lý của bạn trong các bước đơn giản","nextsteps.header":"Các bước tiếp theo có thể của bạn để giải quyết vấn đề","nextsteps.sub_header":"Chúng tôi đã tóm tắt các bước có thể để bạn thực thi khiếu nại của mình tại đây. Nhấp vào từng hộp một để tìm hiểu thêm và nhận được hỗ trợ.","ns.info_2contact_landlord":"**Warum?** \n- Ein zweites Schreiben kann Ihrer Forderung Nachdruck verleihen.\n- Wenn Sie keine Reaktion von Ihrer Vermieter:in erhalten haben, hat sie Ihre Mängelanzeige vielleicht auch nur übersehen.\n- Wenn neue Kosten entstanden sind (z.B. Aufwendungen durch eigene Mängelbeseitigung), können Sie diese direkt von Ihrer Vermieter:in einfordern.  \n###  \n**Wie gehe ich vor?** \n- Kontaktieren Sie Ihre Vermieter:in und weisen sie erneut auf das Problem hin. \n- Benennen Sie klar Ihre Forderung, und setzen Sie eine letzte Frist zur Mängelbeseitigung oder Zahlung, bevor Sie weitere rechtliche Schritte ergreifen.\n- Bleiben Sie dabei freundlich und höflich.","ns.info_ca":"**Warum?** \n- Wenn Sie Ihre Fluglinie mit einem korrekten Anspruch kontaktieren, bezahlt diese meistens direkt Ihre Forderung. \n- Mit einer schriftlichen Aufforderung haben Sie außerdem einen Nachweis für den weiteren Prozess.  \n###  \n**Wie gehe ich vor?** \n- Nutzen Sie das Kontaktformular Ihrer Fluglinie für EU-Fluggastrechte und stellen Sie Ihre Forderung. \n- Kontaktformulare finden Sie auf den Webseiten aller großen Fluglinien, beispielsweise bei [Ryanair](https://onlineform.ryanair.com/de/de/eu-261), [Lufthansa](https://www.lufthansa.com/de/de/feedback), \n[British Airways](https://www.britishairways.com/travel/customerportal/public/de_gb#/customer-service-portal), [AirFrance-KLM](https://wwws.airfrance.de/claim) oder [easyJet](https://www.easyjet.com/de/claim/EU261).\n- Wenn Sie kein Formular finden, können Sie Ihre Forderung auch per Post oder E-Mail stellen. Schildern Sie dabei Ihren Fall konkret und setzen Sie eine Frist für die Zahlung, zum Beispiel zwei bis drei Wochen. \n- Nach Ablauf der Frist können Sie weitere Schritte unternehmen.","ns.info_consultation":"**Warum?** \n- In einer Rechtsberatung kann Ihr Fall individuell von einer Expert:in bewertet werden. \n- Diese kann außerdem erneut Kontakt mit Ihrer Vermieter:in aufnehmen. \n- Manchmal kann beispielsweise ein Schreiben einer Anwält:in helfen, die Vermieter:in von Ihren Ansprüchen zu überzeugen. \n###  \n**Wie gehe ich vor?** \n- In vielen Städten gibt es örtliche Beratungsangebote für Mieter:innen. \n- Einige Beratungsstellen sind kostenlos erreichbar, insbesondere für eine Erstberatung oder finanzschwache Personen. \nAuf der nächsten Seite geben wir Ihnen einen Überblick zu Beratungsstellen.","ns.info_contact_landlord":"**Warum?** \n- Als Mieter:in sind Sie verpflichtet, Mängel in Ihrer Wohnung zu melden.\n- Meistens beseitigen Vermieter:innen einen Mangel, sobald sie benachrichtigt werden. \n- Mit einer schriftlichen Mängelanzeige haben Sie außerdem einen Nachweis für den weiteren Prozess.  \n###  \n**Wie gehe ich vor?** \n- Kontaktieren Sie Ihre Vermieter:in und schildern Sie den Mangel sachlich. \n- Benennen Sie klar Ihre Forderung, und setzen Sie Ihrer Vermieter:in eine realistische Frist, um den Mangel zu beseitigen. \n- Geben Sie an die Miete nur noch unter Vorbehalt zu zahlen, wenn ein Anspruch auf Mietminderung für Sie möglich erscheint.\n- Bleiben Sie dabei freundlich und höflich.  \n###  \nHier helfen wir Ihnen, eine Mängelanzeige zu erstellen:","ns.info_docu":"**Warum?** \n- Um einen Nachweis für Ihre Vermieter:in und den weiteren Prozess zu haben, sollten Sie den Mangel dokumentieren.  \n###  \n**Wie gehe ich vor?** \n- Ist der Mangel sichtbar, bspw. ein Schimmelfleck oder Wasserschaden, können Sie Fotos oder Videos von allen betroffenen Bereichen der Wohnung machen. \n- Ist der Mangel nicht sichtbar, bspw. bei Lärmbelästigung, können Sie aufschreiben, wann und in welcher Form dieser auftritt. Auch ein Video oder Zeug:innen können helfen.","ns.info_file_claim":"**Warum?** \n- Eine Klage ist der letzte Weg, Ihre Ansprüche durchzusetzen, wenn die Gegenseite diese nicht freiwillig erfüllt. \n- Mit einer Klage beantragen Sie, dass eine Richter:in Ihren Fall vor Gericht entscheidet.  \n###  \n**Wie gehe ich vor?** \n- Zuerst können Sie sich bei uns über den Ablauf und die Kosten eines Gerichtsprozesses informieren.\n- Dann helfen wir Ihnen, eine Klageschrift zu erstellen und einzureichen.\n- Vor Gericht versucht die Richter:in, eine Lösung für Sie und die Gegenseite zu finden.\n- Können Sie sich nicht einigen, wird die Richter:in mit einem Urteil über Ihren Fall entscheiden. \n###  \nWichtig ist, dass Sie die **Gegenseite schon direkt aufgefordert** haben, Ihre Ansprüche zu erfüllen.\nHier informieren wir Sie über den Ablauf einer Klage und helfen Ihnen bei der Einreichung.","ns.info_s":"**Warum?** \n- Wenn Sie Ihre Fluglinie bereits kontaktiert haben, und diese Ihrer Forderung nicht nachgekommen ist, kann eine Schlichtung helfen.\n- Eine Schlichtung verursacht keine Kosten, die Entscheidung ist aber nicht verbindlich für die Fluglinie.\n###  \n**Wie gehe ich vor?** \n- Stellen sie zwei Monate nach Ihrer ersten Kontaktaufnahme mit der Fluglinie einen Schlichtungsantrag.\n- Für die [meisten europäischen Fluglinien](https://soep-online.de/assets/files/20.07.soep-Mitgliederliste.pdf) können Sie einen [Antrag bei der söp (Schlichtungsstelle für den öffentlichen Personenverkehr e.V.)](https://soep-online.de/ihre-beschwerde/) stellen. Ansonsten ist die [Schlichtungsstelle Luftverkehr beim Bundesamt für Justiz](https://www.bundesjustizamt.de/DE/Themen/Buergerdienste/Luftverkehr/Schlichtungsstelle_node.html) für Ihren Fall zuständig.\n- Wenn Ihr Antrag zulässig und berechtigt ist, macht die Stelle eine Schlichtungsempfehlung an Sie und die Fluglinie.\n- Das gesamte Schlichtungsverfahren dauert etwa drei Monate. Danach können Sie immer noch Klage einreichen.","pe.header":"Mögliche rechtliche Ansprüche","pe.sub_header":"Aus Ihren Angaben könnten sich die folgenden Ansprüche ergeben. Ein Anspruch ist das Recht, etwas von einer anderen Person oder einem Unternehmen zu fordern. Dabei können Sie auch mehrere Ansprüche gleichzeitig durchsetzen. \nKlicken Sie auf die Ansprüche, um mehr darüber zu erfahren. Auf der nächsten Seite informieren wir Sie dann über mögliche nächste Schritte.","pe.to_next_steps":"Zu den nächsten Schritten","zpo.heading":"Eine Klage am Amtsgericht einreichen","zpo.heading_info":"Mit einer Klage beginnen Sie einen Gerichtsprozess. Was das bedeutet, erklären wir ihnen hier. Am Ende helfen wir Ihnen, eine Klage zu erstellen und einzureichen.","zpo.info1":"### Wie läuft ein Gerichtsprozess ab?\n- Wenn Sie einen Konflikt mit einer Person oder einem Unternehmen nicht allein klären können, kann eine Richter:in diesen in einem Gerichtsprozess für Sie lösen.\n- Im Prozess stellen Sie und die andere Partei den Fall vor der Richter:in dar. Wenn nicht klar ist, welche Version stimmt, werden Beweise erhoben. \n- Zunächst kann die Richter:in Ihnen und der Gegenpartei eine Einigung (Vergleich) vorschlagen, um den Streit schnell beizulegen.\n- Wird keine Einigung gefunden, verkündet die Richter:in am Ende des Prozesses ein verbindliches Urteil. Darin steht, welche Ansprüche wem zustehen.\n- Insgesamt dauert ein Prozess üblicherweise zwischen drei und zehn Monaten.","zpo.info2":"### Was kostet ein Gerichtsprozess?\n- Die Verlierer:in des Prozesses trägt die Kosten. Wenn keine Partei ganz Recht bekommt, werden die Kosten aufgeteilt.\n- Die Höhe der Kosten ist abhängig vom Wert der verhandelten Forderung.\n- Wenn Sie eine Rechtsschutzversicherung haben, kann diese Ihre Kosten übernehmen.\n- Sie können [Prozesskostenhilfe](https://www.bmjv.de/DE/Themen/GerichtsverfahrenUndStreitschlichtung/Prozesskostenhilfe/Prozesskostenhilfe_node.html) beantragen, wenn die Kosten für Sie zu hoch sind.","zpo.info3":"### Wer kann klagen?\n- Jede Person ab 18 Jahren kann alleine am Amtsgericht klagen.\n- Wenn Sie sich dabei unsicher fühlen, können Anwält:innen Sie beraten oder ganz vertreten.\n- Eltern können auch für ihre minderjährigen Kinder klagen, zum Beispiel bei Ansprüchen aus der EU-Fluggastrechteverordnung.","zpo.nextsteps":"1. Auf der kommenden Seite können Sie das Dokument für eine Klage zu Ihrem Fall erstellen.  \n2. Dabei schätzen wir die Gerichtskosten für Sie ab und informieren Sie über die nötigen nächsten Schritte.  \n3. Nach der Erstellung können Sie alle Informationen zunächst überprüfen. **Über die Einreichung entscheiden Sie erst dann.**","zpo.subheading":"Die wichtigsten Informationen auf einen Blick","Über Ansprüche informieren":"Thông báo về khiếu nại"}};